.navContainer {
    background-color: rgb(39, 39, 39);
    display: flex;
    align-items: center;
    height: 70px;
  }

.navLinkCont{
display: flex;
justify-content: space-between;
width: 15rem;
}

.linkContainer {
    display: flex;
    width: 14rem;
    justify-content: space-between;
    align-items: center;
    margin-left: 8vw;
  }

  .linkHome,
.linkCrear {
  color: whitesmoke;
  text-decoration: none;
}

.linkHome {
  font-size: 15px;
  color: #e1e1e1;
  font-family: inherit;
  font-weight: 600;
  cursor: pointer;
  position: relative;
  border: none;
  background: none;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-duration: 400ms;
  transition-property: color;
}

.linkHome:focus,
.linkHome:hover {
  color: #fff;
}

.linkHome:focus:after,
.linkHome:hover:after {
  width: 100%;
  left: 0%;
}

.linkHome:after {
  content: "";
  pointer-events: none;
  bottom: -2px;
  left: 50%;
  position: absolute;
  width: 0%;
  height: 2px;
  background-color: #fff;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-duration: 400ms;
  transition-property: width, left;
}

